componentDidMount() {
  const { loading } = this.state;
    this.unregisterAuthObserver = firebase.auth().onAuthStateChanged(
      (user) => {
        if(user) {
          if(loading) {
            base.syncState(`/${user.uid}`, {
              context: this,
              state: 'data'
            });
            this.getDataBase(user.uid);
          }
          this.setState({
            isSignedIn: !!user, 
            userId: user.displayName,
            userUid: user.uid
          });
        } else {
          return null
        }
      }
    );
  
  //dtection du changement de taille d'cran
  this.handleResize();
  window.addEventListener('resize', this.handleResize);
}

componentWillUnmount() {
  window.removeEventListener('resize', this.handleResize); //on enlve l'vnement qui regarde la taille de la fentre
  this.unregisterAuthObserver();
}

  animFormButton = button => {
    button.classList.add('formButton-active');
    setTimeout(() => {
        button.classList.remove('formButton-active');
    }, 500);
  }

  animFormElt = elt => {
    elt.classList.add('message-enter');
  }

  getDataBase = myUser => {
    const { userId, choixUserUid, newUserUid } = this.state;
    const userUid = myUser || this.state.userUid;
    const currentUser = choixUserUid ? userUid : newUserUid;
    const ref = firebase.database().ref(`${currentUser}`);
        ref.on('value', snapshot => {
          const listItem = [];
          if(snapshot.val() !== null) {
            firebase.database().ref(`${userUid}/user/`).on('value', snapshot => {
              this.setState({ 
                help: snapshot.val().help,
                inverseProduit: snapshot.val().inverseProduit,
                infinitScroll: snapshot.val().infinitScroll,
                userUid: snapshot.val().userUid,
                newUserUid: snapshot.val().newUserUid,
                choixUserUid: snapshot.val().choixUserUid,
                currentUserUid: snapshot.val().choixUserUid === true ? snapshot.val().userUid : snapshot.val().newUserUid,
                linkName: snapshot.val().linkName,
                followers: snapshot.val().followers,
                followWindow: snapshot.val().followWindow
               });
               if(snapshot.val().choixUserUid) {
                firebase.database().ref(`${currentUser}`).on('value', snapshot => {
                  Object.keys(snapshot.val()).map(item =>
                    listItem.push(item)
                  );
                  this.setState({
                    listeDeBase: listItem,
                    // data: snapshot.val(),
                    loading: false
                    });
                });
               } else {
                const newUserUid = snapshot.val().newUserUid;
                firebase.database().ref(`${newUserUid}`).on('value', snapshot => {
                  Object.keys(snapshot.val()).map(item =>
                    listItem.push(item)
                  );
                  this.setState({
                    listeDeBase: listItem,
                    // data: snapshot.val(),
                    loading: false
                    });
                });
               }
            });
            
            } else {
              firebase.database().ref(`${userUid}`).set({
                user: {
                  userName: userId,
                  userUid: userUid,
                  newUserUid: '',
                  infinitScroll: false,
                  help: true,
                  inverseProduit: true,
                  choixUserUid: true,
                  linkName: '',
                  followers: [],
                  followWindow: true
                }
              });
              this.setState({
                loading: false
            });
          }
        });
  }

  //charger la database du compte slectionn
  getOtherDataBase = linkName => {
    const { currentUserUid } = this.state;
    const user = this.state.userId;
    const ref = firebase.database().ref(`${currentUserUid}`);
        ref.on('value', snapshot => {
          const listItem = [];
          if(snapshot.val() !== null) {
            firebase.database().ref(`${currentUserUid}/user`).on('value', snapshot => {
              this.setState({ 
                linkName: snapshot.val().userName === user ? linkName : snapshot.val().userName  //probleme quand retour sur user d'origine
               });
            });
            firebase.database().ref(`${currentUserUid}`).on('value', snapshot => {
              Object.keys(snapshot.val()).map(item =>
                listItem.push(item)
              );
                this.setState({
                  listeDeBase: listItem,
                  data: snapshot.val(),
                  loading: false
                  });
              });
            }
        });
        
  }

  // ----> a dcommenter pour charger une liste d'exemple dans la bdd <------
