import React, { Component } from 'react';

import '../animations.css';
import '../NewApp.css';

import Item from './Item';

import { List, Row, Col, Icon, Button, Drawer, Input } from 'antd';

class ItemList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            newArrayData: []
        }
    }

    componentDidMount() {
        // on charge la liste en fonction du de infinitScroll ou non
        if(this.props.infinitScroll) {
            this.props.loadItems();
        } else {
            this.renderData();
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if(this.props.data && !this.props.infinitScroll) {
            if(prevProps.data !== this.props.data) {
                this.renderData();
              }
        }
        
        if(this.nameInput) {
            this.nameInput.focus();
        }
        if(this.nameInput2) {
            this.nameInput2.focus();
        }
        if(this.nameInput3) {
            this.nameInput3.focus();
        }
    }

    renderData = () => {
        const { currentTitre, inverseProduit, data } = this.props;
        const newArrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data)
            .map(item => item === currentTitre ? 
                Object.keys(data[item])
                    .map(category => Object.keys(data[item][category])
                        .map(categoryItem => newArrayData.push(data[item][category][categoryItem])))
                : null);
        if(inverseProduit) {
            newArrayData.reverse();
        }
        this.setState({ newArrayData });
    }

    liste() {
        const { inputOpen, currentCategory, categoryIsChanged,
                categorySelected, valideChangeCategory, arrayData,
                infinitScroll
            } = this.props;
        const { newArrayData } = this.state;
        const myData = infinitScroll ? arrayData : newArrayData;
        if(!myData) {
            return null
        }
        return (myData || []).map((elt, id) => 
        <>
            {
                elt ?
                <List
                key={`listId-${id}`}
                id={elt.titreCat}
                name={`name-${elt.titreCat}`}
                locale={{ emptyText: 'Veuillez ajouter un produit' }}
                header={
                    <Row type="flex" justify="space-between" align='middle'>
                    {
                        categoryIsChanged && categorySelected === elt.key ?
                        <Col xs={infinitScroll ? 16 : 12} sm={infinitScroll ? 18 : 15} lg={infinitScroll ? 20 : 18} className='titreCat' onClick={() => this.props.changeCategory(elt)}>
                            <Input
                                type='text' 
                                id='myInput'
                                maxLength={40}
                                value={elt.titreCat} 
                                placeholder='Modifier la catgorie' 
                                onChange={e => this.props.handleChangeCategory(e, elt)} 
                                onKeyUp={e => valideChangeCategory(e, elt)}
                                ref={input => { this.nameInput3 = input }} 
                            />
                        </Col>
                        :
                        <Col id={elt.titreCat} xs={infinitScroll ? 16 : 12} sm={infinitScroll ? 18 : 15} lg={infinitScroll ? 20 : 18} className='titreCat' onClick={() => this.props.changeCategory(elt)}>
                            {elt.titreCat}
                        </Col>
                    }
                    {
                        infinitScroll ?
                        null
                        :
                        <Col xs={4} sm={3} lg={2}>
                            <Button 
                                style={{ background: 'transparent' }}
                                name="isVisible" 
                                className="trash"
                                type='primary'
                                block
                                onClick={() => this.props.checkCurrentCategory(elt.titreCat, elt.showCategory)}>
                                {
                                    elt.showCategory ?
                                    <Icon type="caret-up" />
                                    :
                                    <Icon type="caret-down" />
                                }
                            </Button>
                        </Col>
                    }
                        <Col xs={4} sm={3} lg={2}>
                            <Button 
                                style={{ background: 'transparent' }}
                                name="isVisible" 
                                className="trash"
                                type='primary'
                                block
                                onClick={() => this.props.categoryVisible(elt)}>
                                    {
                                        elt.categoryIsVisible ?
                                        <i className="far fa-eye" style={{ color: 'aliceblue' }}/>
                                        :
                                        <i className="far fa-eye-slash" style={{ color: 'aliceblue' }}/>
                                    }
                            </Button>
                        </Col>
                        <Col xs={4} md={3} lg={2}>
                            <Button 
                                name="trash" 
                                className="trash"
                                block
                                type='primary'
                                onClick={() => this.props.deleteCategory(elt, id)}>
                                <Icon type='delete' style={{ color : '#FFF', fontSize: '1.4em' }}/>
                            </Button>
                        </Col>
                    </Row>
                }
                footer={
                    <>
                    {
                        inputOpen && currentCategory === elt.titreCat ? 
                        <Row type="flex" justify="center" align='middle'>
                            <form className='form form-titre' id='myForm' onSubmit={ e => this.props.createNewItem(e, id)}>
                                <input 
                                    id='myInput' 
                                    type='text' 
                                    placeholder='Ajouter un produit'
                                    maxLength={40} 
                                    autoFocus={true} 
                                    ref={input => { this.nameInput2 = input }} 
                                />
                                <button type='submit' id='buttonSubmit'>
                                    <Icon type="plus" />
                                </button>
                            </form>
                        </Row>
                        :
                        <Row 
                            type="flex" 
                            justify="center" 
                            align='middle' 
                            onClick={() => this.props.openInput(elt.titreCat)}
                            >
                            <p>Ajouter un produit</p>
                        </Row>
                    }
                    </>
                }
                bordered
                dataSource={Object.keys(elt.categoryItems || []).map(item => elt.categoryItems[item]).reverse()}
                renderItem={(item, id) => (
                    <>
                    {
                        infinitScroll ?
                        
                            item ?
                            <List.Item key={`listItemId-${id}`} id={elt.titreCat} className={item.checked ? '.ant-list-item flip' : '.ant-list-item'}>
                                <Item
                                    item={item.item}
                                    itemKey={item.key}
                                    comment={item.comment}
                                    count={item.count}
                                    itemIsVisible={item.itemIsVisible}
                                    itemIsChanged={this.props.itemIsChanged}
                                    itemSelected={this.props.itemSelected}
    
                                    handleChangeItem={e => this.props.handleChangeItem(e, item)}
                                    valideChangeItem={e => this.props.valideChangeItem(e, item)}
                                    changeItem={() => this.props.changeItem(item)}
                                    toggleComment={() => this.props.toggleComment(item)}
                                    itemVisible={() => this.props.itemVisible(item)}
                                    deleteItem={() => this.props.delete(item)}
                                />
                            </List.Item>
                            :
                            null
                        
                        :
                            elt.showCategory && item ?
                            <List.Item key={`listItemId-${id}`} id={elt.titreCat} className={item.checked ? '.ant-list-item flip' : '.ant-list-item'}>
                                <Item
                                    item={item.item}
                                    itemKey={item.key}
                                    comment={item.comment}
                                    count={item.count}
                                    itemIsVisible={item.itemIsVisible}
                                    itemIsChanged={this.props.itemIsChanged}
                                    itemSelected={this.props.itemSelected}
    
                                    handleChangeItem={e => this.props.handleChangeItem(e, item)}
                                    valideChangeItem={e => this.props.valideChangeItem(e, item)}
                                    changeItem={() => this.props.changeItem(item)}
                                    toggleComment={() => this.props.toggleComment(item)}
                                    itemVisible={() => this.props.itemVisible(item)}
                                    deleteItem={() => this.props.delete(item)}
                                />
                            </List.Item>
                            :
                            null
                        
                    }
                    </>
                    
                    )}
            />
            : 
            null
            }
            
        </>
        );
    }
    render() { 
        const { item, largeur, visible, showForm, arrayData } = this.props;
        const drawerWidth = largeur > 720 ? '50%' : '90%';
        return ( 
            <>
            {
                showForm ?
                    <form className='form form-titre' id='myForm' onSubmit={ e => this.props.createNewCategory(e)}>
                        <input 
                            id='myInput2'
                            type='text' 
                            placeholder='Ajouter une catgorie'
                            maxLength={40} 
                            autoFocus={true} 
                            ref={input => { this.nameInput = input }} 
                        />
                        <button type='submit' id='buttonSubmit'>
                            <Icon type="plus" />
                        </button>
                    </form>
                :
                <>
                <Row 
                    className='ant-list-footer addCategory' 
                    type="flex" 
                    justify="center" 
                    align='middle'
                    onClick={this.props.showFormulaire}
                    >
                    <p>Ajouter une catgorie</p>
                </Row>
                {
                    arrayData.length === 0 ?
                    <Col xs={{ span: 6}} sm={{ span: 5}} md={{ span: 4}} lg={{ span: 6}}>
                        <Button block type="primary" className='reload' onClick={this.props.loadListExemple}>
                            <Icon type="download" />
                            Charger
                        </Button>
                    </Col>
                    :
                    null
                }
                </>
            }
            {this.liste()}
                <Drawer
                    placement="right"
                    width={drawerWidth}
                    onClose={(e) => this.props.onClose(e, item)}
                    visible={visible}
                    >
                    {
                        visible ?
                        this.props.modalContent(item)
                        :
                        null
                    }
                </Drawer>
            </>
         );
    }
}
 
export default ItemList;