import React, { Component } from 'react';

import '../NewApp.css';

import { Button, notification } from 'antd';

const openNotification = () => {
    notification.open({
        message: 'Liste de course interactive',
        description: "Bienvenue sur votre application de liste de course. Pour commencer, veuillez cliquer sur \"crer une liste\". Si vous avez dj cr une liste, cliquez sur \"En course\" lorsque vous faites vos course.",
        duration: 0,
        style: {
            textAlign: 'justify'
          }
      });
  };

class Menu extends Component {
    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
    }

    render() { 
        return ( 
            <>
                        <Button 
                            style={{ margin : '1em 0'}}
                            size='large'
                            block 
                            type="primary"
                            className='item-button'
                            name='enCourse'
                            onClick={this.props.choixComponent}
                            >Faire ses courses
                        </Button>
                        <Button 
                            style={{ margin : '0 0 1em 0'}}
                            size='large'
                            block 
                            type="primary"
                            className='item-button'
                            name='mesListes'
                            onClick={this.props.choixComponent}
                        >Crer une liste
                        </Button>
                        <Button 
                            style={{ margin : '0 0 1em 0'}}
                            size='large'
                            block 
                            type="primary" 
                            className='item-button'
                            name='monProfil'
                            onClick={this.props.choixComponent}
                        >Mon profil
                        </Button>
            </>
         );
    }
}
 
export default Menu;