import React, { Component } from 'react';

import { Col, Progress } from 'antd';

class EnCourseItem extends Component {
    render() { 
        const { item, choixListe, countPercent, countTotal, countItemPos } = this.props;
        return ( 
            <Col span={24} key={`key-${item}`} className='listeDeBaseEnCourse'>  
                <Col xs={{ span: 20 }} md={{ span: 21 }} lg={{ span: 22 }} key={`elt-${item}`} id={`id-${item}`} onClick={choixListe}>
                    {item} - <span>{countItemPos}/{countTotal}</span>
                    <Progress percent={countPercent} showInfo={false} strokeColor='#009688' />
                </Col>
            </Col>
         );
    }
}
 
export default EnCourseItem;